.show-more {
  position: relative;
  margin-top: 78px;
}

.show-more__icon {
  position: absolute;
  content: "";
  top: -50px;
  left: 145px;
}

.show-more__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}

.show-more__text {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}
